// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/states.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import './mixins.scss';

.eureka-checkbox, %eureka-checkbox {
    width: 24px;
    height: 24px;
    box-sizing: border-box;
    border-radius: var(--eureka-form-checkbox-border-radius, eureka-light-theme('form-checkbox-border-radius'));
    position: relative;
    display: block;

    &__input {
        opacity: 0;
        z-index: 3;
    }

    &__container {
        background-color: var(--eureka-form-input-color-bg, eureka-light-theme('form-input-color-bg'));
        box-sizing: border-box;
        border: 1px solid var(--eureka-core-color-universal-accessible-border, eureka-light-theme('core-color-universal-accessible-border'));
        border-radius: var(--eureka-form-checkbox-border-radius, eureka-light-theme('form-checkbox-border-radius'));
        display: block;
        z-index: 1;
    }

    &__input,
    &__container {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }

    &__input:disabled {
        cursor: not-allowed;
    }

    &__input:focus-visible + &__container {
        @include eureka-focus-state-box-shadow(var(--eureka-form-input-focus-color-halo, eureka-light-theme('form-input-focus-color-halo')));
        border-color: var(--eureka-form-input-focus-color-border, eureka-light-theme('form-input-focus-color-border'));
    }

    &__input:disabled + &__container {
        @include eureka-form-input-disabled(false);
    }

    &__input:checked + &__container {
        color: var(--eureka-form-input-color-bg, eureka-light-theme('form-input-color-bg'));
        background-color: var(--eureka-form-checked-color-accent, eureka-light-theme('form-checked-color-accent'));
        border-color: var(--eureka-form-checked-color-accent, eureka-light-theme('form-checked-color-accent'));
    }

    &__icon {
        font-size: 24px;
        margin-top: 50%;
        margin-left: 50%;
        transform: translate3d(-50%, -50%, 0);
        position: absolute;
        z-index: 2;
    }
}
