// ========================================================
// Dependencies
// ========================================================
@use 'sass:color';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/states.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import './mixins.scss';

.eureka-text-input, %eureka-text-input {
    @include eureka-large-text;
    @include eureka-focus-state-halo(var(--eureka-form-input-focus-color-halo, eureka-light-theme('form-input-focus-color-halo')));
    color: var(--eureka-form-input-color-text, eureka-light-theme('form-input-color-text'));
    background-color: var(--eureka-form-input-color-bg, eureka-light-theme('form-input-color-bg'));
    box-sizing: border-box;
    box-shadow: inset 0 1px 1px color.change(eureka-light-theme('core-color-black'), $alpha: 0.075);
    border: 1px solid var(--eureka-core-color-universal-accessible-border, eureka-light-theme('core-color-universal-accessible-border'));
    border-radius: var(--eureka-form-input-border-radius, eureka-light-theme('form-input-border-radius'));
    line-height: var(--eureka-core-typography-line-height, eureka-light-theme('core-typography-line-height'));
    padding: eureka-light-theme('form-input-padding');
    $this: '.eureka-text-input';

    &:focus-visible {
        border-color: var(--eureka-form-input-focus-color-border, eureka-light-theme('form-input-focus-color-border'));
    }

    &:disabled {
        @include eureka-form-input-disabled;
    }

    &[aria-invalid] {
        @include eureka-form-input-error;
    }

    &::placeholder {
        color: var(--eureka-form-input-placeholder-color-text, eureka-light-theme('form-input-placeholder-color-text'));
        font-style: var(--eureka-form-input-placeholder-font-style, eureka-light-theme('form-input-placeholder-font-style'));
    }

    @at-root .eureka-floating-label-composer__control-container & {
        width: 100%;
    }
}
